export default {
	data() {
		return {
			picList: [],
		};
	},
	onLoad(query) {
		// 获取图片列表数据
		this.picList=uni.getStorageSync("imagePiclist");
	},
	onUnload() {
		uni.removeStorageSync("imagePiclist");
		uni.removeStorageSync("currentImgIndex");
	},
	methods: {
		clickPic(index) {
			uni.setStorageSync("currentImgIndex",index);
			uni.navigateTo({
				url: '/pages/LYFW/currency/imgPreview'
			});
		}
	}
};
